import React from 'react';

const Main = () => {
    const str = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz1234567890!@#$%^&*()_+=[]{}'
    const [ready, setReady] = React.useState(false)
    const [password, setPassword] = React.useState('')

    function getRandomInt(max) {
        return Math.floor(Math.random() * max);
    }
    function getInterval(min, max) {
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min)) + min;
    }
    const copyToClipboard = (str) => {
        const temp = document.createElement('textarea');
        temp.value = str;
        temp.setAttribute('readonly', '');
        temp.style.position = 'absolute';
        temp.style.left = '-9999px';
        document.body.appendChild(temp);
        const selected =
            document.getSelection().rangeCount > 0
                ? document.getSelection().getRangeAt(0)
                : false;
        temp.select();
        document.execCommand('copy');
        document.body.removeChild(temp);
        if (selected) {
            document.getSelection().removeAllRanges();
            document.getSelection().addRange(selected);
        }
    };
    const createPassword = () => {
        setPassword('')
        setReady(true)
        for (let i = 0; i < getInterval(10,35); i++) {
             setPassword(prev => prev + str[getRandomInt(str.length-1)] )
        }

    }
    return (
        <main className='main'>
            <p>On this webpage you can generate random password for all your needs!</p>
            <button onClick={createPassword} className='main-btn'>
                Generate
            </button>
            <h3>
                <span>Your password is: </span>
                {ready ?
                    <>
                        <span className='password'>{password}</span>
                        <button onClick={copyToClipboard(password)} className='copy-btn'>Copy</button>
                    </>
                    :
                    '...'
                }
            </h3>
        </main>
    );
};

export default Main;